<template>
  <div class="settings-main-box">
    <div class="header">
      <i class="iconfont icon-zhiyuanfanhui1" @click="goBack"></i>
      <span>菜单编辑</span>
    </div>
    <div class="food-list-box">
      <div class="food-item" v-for="(item, index) in foodList" :key="index">
        <span>{{ item }}</span>
        <i
          class="iconfont icon-cuowu"
          @click="showDeleteModal(item, index)"
        ></i>
      </div>
      <div class="food-item new" key="new" @click="showAddModal">
        + 菜单新增
      </div>
    </div>
    <div class="action-button" @click="save">保存</div>

    <van-popup v-model="addModal">
      <div class="add-box">
        <div class="title">菜单新增</div>
        <input type="text" v-model="text" placeholder="猪猪想吃啥呀" />
        <div class="bottom-button-box">
          <div class="cancel button-item" @click="addModal = false">取消</div>
          <div class="confirm button-item" @click="confirmAdd">确认</div>
        </div>
      </div>
    </van-popup>

    <van-popup v-model="deleteModal">
      <div class="add-box">
        <div class="title">提示</div>
        <div class="tips">确认要删除{{ temp.name }}吗？</div>
        <div class="bottom-button-box">
          <div class="cancel button-item" @click="deleteModal = false">
            取消
          </div>
          <div class="confirm button-item" @click="confirmDelete()">确认</div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import Header from "../components/Header.vue";

export default {
  name: "index",
  components: { Header },
  data() {
    return {
      addModal: false,
      deleteModal: false,
      text: "",
      temp: {
        name: "",
        index: 0,
      },
      foodList: [
        "拌粉",
        "凉皮",
        "麻辣烫",
        "新丰小吃",
        "汉堡王",
        "螺蛳粉",
        "百味鸡",
        "砂锅",
        "咬不得",
      ],
    };
  },
  activated() {
    this.initFoodList();
  },
  methods: {
    initFoodList() {
      if (localStorage.getItem("foodList")) {
        this.foodList = JSON.parse(localStorage.getItem("foodList"));
      }
    },

    save() {
      localStorage.setItem("foodList", JSON.stringify(this.foodList));
      this.goBack();
    },

    showAddModal() {
      this.text = "";
      this.addModal = true;
    },

    confirmAdd() {
      this.foodList.push(this.text);
      this.addModal = false;
    },

    confirmDelete() {
      this.foodList.splice(this.temp.index, 1);
      this.deleteModal = false;
    },

    showDeleteModal(name, index) {
      this.temp.name = name;
      this.temp.index = index;
      this.deleteModal = true;
    },

    goBack() {
      this.$router.back(-1);
    },
  },
};
</script>

<style lang="less" scoped>
.settings-main-box {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f5f5f5;
  overflow: auto;

  .header {
    width: 100%;
    height: 88px;
    background: #02a0df;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: normal;
    font-size: 36px;
    color: #ffffff;
    letter-spacing: 0;
    text-align: center;
    margin-bottom: 50px;

    .icon-zhiyuanfanhui1 {
      font-size: 40px;
      position: absolute;
      left: 30px;
    }
  }

  .food-list-box {
    padding: 0 30px;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .food-item {
      position: relative;
      padding: 8px 0;
      margin-bottom: 20px;
      border-radius: 8px;
      width: 46%;
      font-size: 30px;
      background-color: #fff;
      border: 1px solid #c0c4cc;
      text-align: center;

      .icon-cuowu {
        position: absolute;
        right: -16px;
        top: -16px;
        font-size: 32px;
        color: #909399;
      }
    }

    .new {
      font-size: 30px;
      border: 1px dashed #409eff;
      color: #409eff;
    }
  }

  ::v-deep .van-popup {
    .add-box {
      width: 570px;
      padding-top: 50px;
      display: flex;
      flex-direction: column;
      align-items: center;

      .title {
        font-weight: 500;
        font-size: 36px;
        color: #373737;
        letter-spacing: 0;
        font-weight: bold;
        text-align: center;
      }

      input {
        margin-top: 20px;
        font-weight: 400;
        font-size: 30px;
        color: #b0b0b0;
        letter-spacing: 0;
        border-bottom: 2px solid #eaecef;
        padding-bottom: 10px;
        border-right: none;
        border-left: none;
        border-top: none;
        outline: none;
        text-align: center;
        margin-bottom: 50px;
        &::placeholder {
          color: rgba(#b0b0b0, 0.6);
        }
      }

      .tips {
        margin-top: 20px;
        margin-bottom: 50px;
        font-weight: 400;
        font-size: 30px;
        color: #666666;
        letter-spacing: 0;
      }

      .bottom-button-box {
        width: 100%;
        border-top: 2px solid #eaecef;
        height: 80px;
        display: flex;
        align-items: center;
        .button-item {
          height: 100%;
          flex: 1;
          display: flex;
          align-items: center;
          justify-content: center;
          font-weight: 400;
          font-size: 24px;

          &:first-child {
            border-right: 2px solid #eaecef;
          }
        }

        .cancel {
          color: #787878;
        }
        .confirm {
          color: #3c9afc;
        }
      }
    }
  }

  .action-button {
    margin-top: 6vh;
    width: 200px;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 36px;
    background-color: rgba(#fff, 0.6);
    backdrop-filter: blur(4px);
    border-radius: 18px;
    color: #fff;
    background-color: #409eff;
  }

  .loading {
    background-color: #909399;
  }
}
</style>
