<template>
  <div class="index-main-box">
    <header>
      <div class="title-box">
        <i class="iconfont icon-zhu"></i>
        <span>猪猪菜单</span>
      </div>
      <div class="settings-box" @click="$router.push('/settings')">
        <i class="iconfont icon-shezhi"></i>
        <span>设置菜单</span>
      </div>
    </header>
    <div class="options-box">{{ foodList[nowIndex] }}</div>
    <div class="action-button" v-if="!loading" @click="timeToEat">开饭啦</div>
    <div class="action-button loading" v-else>{{ steps }}s后暂停</div>
  </div>
</template>

<script>
import Header from "../components/Header.vue";
import dayjs from "dayjs";

export default {
  name: "index",
  components: { Header },
  data() {
    return {
      loading: false,
      steps: 3,
      timer: null,
      stepsTimer: null,
      nowIndex: 0,
      foodList: [
        "拌粉",
        "凉皮",
        "麻辣烫",
        "新丰小吃",
        "汉堡王",
        "螺蛳粉",
        "百味鸡",
        "吉祥馄饨",
        "咬不得",
      ],
    };
  },
  activated() {
    this.initFoodList();
  },
  beforeDestroy() {
    clearInterval(this.stepsTimer);
    clearInterval(this.timer);
  },
  methods: {
    initFoodList() {
      if (localStorage.getItem("foodList")) {
        this.foodList = JSON.parse(localStorage.getItem("foodList"));
      }
    },

    randomNum(minNum, maxNum) {
      switch (arguments.length) {
        case 1:
          return parseInt(Math.random() * minNum + 1, 10);
          break;
        case 2:
          return parseInt(Math.random() * (maxNum - minNum + 1) + minNum, 10);
          break;
        default:
          return 0;
          break;
      }
    },

    timeToEat() {
      this.steps = this.randomNum(3, 5);
      this.loading = true;
      this.timer = setInterval(() => {
        this.nowIndex = this.randomNum(0, this.foodList.length - 1);
      }, 36);
      this.stepsTimer = setInterval(() => {
        this.steps--;
        if (this.steps === 0) {
          this.loading = false;
          clearInterval(this.stepsTimer);
          clearInterval(this.timer);
        }
      }, 1000);
    },
  },
};
</script>

<style lang="less" scoped>
.index-main-box {
  padding-top: 2vh;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f5f5f5;

  header {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 30px;
    .title-box {
      display: flex;
      align-items: center;
      font-size: 30px;
      color: #f56c6c;

      .icon-zhu {
        font-size: 34px;
        margin-right: 10px;
      }
    }

    .settings-box {
      display: flex;
      align-items: center;

      .iconfont {
        font-size: 30px;
        color: #909399;
        margin-right: 6px;
        padding-top: 4px;
      }

      span {
        font-size: 30px;
        color: #909399;
      }
    }
  }

  .options-box {
    margin-top: 20vh;
    width: 500px;
    height: 200px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 30px;
    box-shadow: 0 8px 20px 2px rgba(#000, 0.1);
    font-size: 60px;
    white-space: nowrap;
    letter-spacing: 4px;
    font-weight: bold;
    color: #606266;
  }

  .action-button {
    margin-top: 12vh;
    width: 300px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    font-size: 46px;
    background-color: rgba(#fff, 0.6);
    backdrop-filter: blur(4px);
    border-radius: 18px;
    color: #fff;
    background-color: #409eff;
  }

  .loading {
    background-color: #909399;
  }
}
</style>
